import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'
// font awesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { faSpinner } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
library.add(faSpinner)
Vue.component('font-awesome-icon', FontAwesomeIcon)

Vue.config.productionTip = false

import '../node_modules/bootstrap/dist/css/bootstrap.min.css'

const apilocationHTTP = 'http://45.79.108.112:8081/emendas';
const apilocationHTTPS = 'https://45.79.108.112:8443/emendas';
const apilocation = apilocationHTTPS;
const apiurls = {
  getMunicipioPorId: function(id) {
    return apilocation+"/api/municipios/ibge/"+id;
  },
  getMunicipiosPorNome: function(nome) {
    return apilocation+"/api/municipios/"+nome;
  },
  getParlamentaresPorMunicipioEAno: function (municipio, ano) {
    return apilocation+"/api/emendas/municipio/parlamentares/"+municipio+"/"+ano;
  },
  getConveniosPorParlamentarEAno: function (parlamentar, ano) {
    return apilocation+"/api/emendas/"+parlamentar+"/"+ano;
  },
  getConvenioPorId: function (convenio) {
    return apilocation+"/api/convenios/"+convenio;
  },
  getEmendaPorId: function (id) {
    return apilocation+"/api/emendas/"+id;
  },
};
Vue.prototype.$http = axios;
Vue.prototype.$appName = "Emendas";
Vue.prototype.$apiUrls = apiurls;

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
