import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/sobre',
      name: 'sobre',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/Sobre.vue')
    },
    {
      path: '/search/:search',
      name: 'search',
      component: () => import('./views/Search.vue'),
      props: true
    },
    {
      path: '/municipio/:id',
      name: 'municipio_anoAtual',
      component: () => import('./views/Municipio.vue'),
      props: true
      /*
      props: (route) => ({ id: route.query.id }),
      params: (route) => ({ id: route.query.id })*/
    },
    {
      path: '/municipio/:id/:ano',
      name: 'municipio',
      component: () => import('./views/Municipio.vue'),
      props: true
      /*
      props: (route) => ({ id: route.query.id }),
      params: (route) => ({ id: route.query.id })*/
    },
    {
      path: '/emenda/:id',
      name: 'emenda',
      component: () => import('./views/Emenda.vue'),
      props: true
    },
  ]
})
