<template>
  <div class="page home">
    <div class="container">
      <h2 class="title page-title mb-5">Descubra quanto sua cidade ganha</h2>
      <form class="form" name="homePageForm" method="GET" v-on:submit.prevent="onSubmit">
        <div class="input-group">
          <input name="s" placeholder="Insira sua Cidade" class="form-control" ref="s" v-on:keyup="onKeyPress">
          <div class="loader" :class="hideLoader">
            <font-awesome-icon icon="spinner" pulse />
          </div>
          <div class="input-group-append">
            <button class="btn btn-primary" type="submit" aria-label="Buscar"><em class="fas fa-search"></em>Buscar</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data: function() {
    return {
      hideLoader: 'hide'
    }
  },
  methods: {
    onSubmit: function() {
      this.$router.push("/search/"+this.$refs.s.value);
    },
    onKeyPress: function() {
      let search = this.$refs.s.value;
      if (search.length>3) {
        this.hideLoader = '';
        this.$http.get( this.$apiUrls.getMunicipiosPorNome(search) ).then((response) => {
          this.municipios = response.data
          this.hideLoader = 'hide'
        }, (error) => {
          this.hideLoader = 'hide';
        });
      }
    }
  }
}
</script>

