    align-items: center;
  }
  .loader {
    background: white;
    border: none;
    outline: none;
    padding: 0 .5em;
    position: absolute;
    right: 5rem;
    top: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    justify-items: center;
    height: 90%;
    transition: all 0.3s ease;
    z-index: 10;
  }
  .loader.hide {
    opacity: 0;
  }
</style>
